import React, { Component } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Form, 
  FormGroup,
  Row,
  Input,
  InputGroup,
  InputGroupAddon,
  ListGroup, 
  ListGroupItem, 
  ListGroupItemHeading, 
  ListGroupItemText,
} from 'reactstrap';
import CONST from '../../../consts'

const getObligationsURL = (page) =>
  CONST.REGULATION_API_URL+`obligations?start=${page}&size=10`;

const RenderTableDataTest = ({ list, page, isLoading, onPaginatedSearch }) =>{
  return list.map((obligation) => {
    let {nombre, descripcion, contenido, titulo, seccion, capitulo, apartado, subApartado } = obligation
    if(titulo != 'null'){titulo = titulo}else{titulo = " "};
    if(seccion != 'null'){seccion = seccion}else{seccion = " "};
    if(capitulo != 'null'){capitulo = capitulo}else{capitulo = " "};
    if(apartado != 'null'){apartado = apartado}else{apartado = " "};
    if(subApartado != 'null'){subApartado = subApartado}else{subApartado = " "};
    
    return(
      <div>
        <ListGroupItem  action>
          <ListGroupItemHeading className="d-flex justify-content-between">
          <h5>{descripcion}</h5>
          <h6 className="text-muted">{nombre}</h6>
          </ListGroupItemHeading>
          <ListGroupItemText>
          <h6 className="font-weight-light">{titulo}</h6>
          <h6 className="font-weight-light">{capitulo}</h6>
          <h6 className="font-weight-light">{seccion}</h6>
          <h6 className="font-weight-light">{apartado}</h6>
          <h6 className="font-weight-light">{subApartado}</h6>
          <br></br>
          {contenido}
          <br></br>
          <br></br>
          </ListGroupItemText>
        </ListGroupItem>
      </div>
    )
  })
}

const applyUpdateResult = (result) => (prevState) => ({
  obligations: [...prevState.obligations, ...result.data],
  page: result.actualPage,
  isLoading: false
});

const applySetResult = (result) => (prevState) => ({
  obligations: [...prevState.obligations, ...result.data],
  page: result.actualPage,
  isLoading: false
});

class ObligationsViewer extends Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.state = {
      keywords: "",
      obligations : [],
      page: null,
      totalPages: '0',
      items: '0',
      isLoading: false,
      search: false
    }
  }
  
  handleInputChange(editEvent){
    this.setState({
      keywords: editEvent.target.value
    })
  }

  handleSubmit(event){
    console.log("Click en Buscar");
    event.preventDefault();
    console.log(this.state.keywords);
    let keywordsToSearch = this.state.keywords
    fetch(CONST.REGULATION_API_URL+"obligations/"+keywordsToSearch)
    .then(response => response.json())
    .then( data => {
      this.setState({page: null})
      this.setState({totalPages: data.totalPages})
      this.setState({items: data.items})
      this.setState({obligations: data.data})
    })
  }

  onPaginatedSearch = (e) =>
    this.fetchObligations(this.state.page + 1);

  fetchObligations = (page) => {
    this.setState({ isLoading: true });
    fetch(getObligationsURL(page))
      .then(response => response.json())
      .then(result => this.onSetResult(result, page));
  }

  onSetResult = (result, page) =>
  page === 0
    ? this.setState(applySetResult(result))
    : this.setState(applyUpdateResult(result));

  renderTableData() {
    return this.state.obligations.map((obligation) => {
      let {nombre, descripcion, contenido, titulo, seccion, capitulo, apartado, subApartado } = obligation
      if(titulo != 'null'){titulo = titulo}else{titulo = " "};
      if(seccion != 'null'){seccion = seccion}else{seccion = " "};
      if(capitulo != 'null'){capitulo = capitulo}else{capitulo = " "};
      if(apartado != 'null'){apartado = apartado}else{apartado = " "};
      if(subApartado != 'null'){subApartado = subApartado}else{subApartado = " "};

      return(
        <ListGroupItem  action>
          <ListGroupItemHeading className="d-flex justify-content-between">
            <h5>{descripcion}</h5>
            <h6 className="text-muted">{nombre}</h6>
          </ListGroupItemHeading>
          <ListGroupItemText>
            <h6 className="font-weight-light">{titulo}</h6>
            <h6 className="font-weight-light">{capitulo}</h6>
            <h6 className="font-weight-light">{seccion}</h6>
            <h6 className="font-weight-light">{apartado}</h6>
            <h6 className="font-weight-light">{subApartado}</h6>
            <br></br>
            {contenido}
            <br></br>
            <br></br>
          </ListGroupItemText>
        </ListGroupItem>
      )
    })
  }

  loading = () => <div className = "animated fadeIn pt-1 text-center" > Loading... </div>

  componentDidMount(){
    fetch(CONST.REGULATION_API_URL+'obligations?start=0&size=10')
    .then( results => {
      return results.json();
    }).then( data => {
      console.log("Obtained REST data: " , data)
      console.log("Obtained Obligations: " , data.data)
      this.setState({page: data.actualPage})
      this.setState({totalPages: data.totalPages})
      this.setState({items: data.items})
      this.setState({obligations: data.data})
      console.log("Estado Actual de State: " , this.state);
    })
  }

    render() {
      return ( 
        <div className = "animated fadeIn" >
          <Row className= "mt-3">
            <Col md={12} xl={12} lg={12}>
              <Card>
                <CardHeader>
                  <strong>Búsqueda de obligaciones específicas</strong>
                </CardHeader>
                <CardBody>
                  <Row>
                    <Col sm={6} md={6} lg={6} xl={6}>
                      <Row>
                        <Col sm={3} md={3} lg={3} xl={3}>
                          <div className="callout callout-info">
                            <h5 className="text-muted">Obligaciones</h5>
                            <strong className="h4">{this.state.items}</strong>
                          </div>
                        </Col>
                        <Col sm={9} md={9} lg={9} xl={9}>
                          <Form onSubmit={this.handleSubmit}>
                            <FormGroup row>
                              <Col md={12} xl={12} lg={12} className="mt-3">
                                <InputGroup>
                                  <Input type="text" id="keywords" name="input-obligacion" placeholder="Obligacion" value={this.state.keywords} onChange={this.handleInputChange}/>
                                  <InputGroupAddon addonType="append">
                                    <Button type="submit" color="primary"><i className="fa fa-search"></i> Buscar</Button>
                                  </InputGroupAddon>
                                </InputGroup>
                              </Col>
                            </FormGroup>
                          </Form>
                        </Col>
                      </Row>
                    </Col>
                    <Col md={12} xl={12} lg={12}>
                      <Col md={12} xl={12} lg={12}>
                        <Card className="border-primary">
                          <CardHeader>
                            <i className="icons font-2xl cui-arrow-bottom"></i> Obligaciones 
                            <small> Resultados de búsqueda</small>
                          </CardHeader>
                          <CardBody>
                            <ListGroup>
                              <RenderTableDataTest
                                list={this.state.obligations}
                                isLoading={this.state.isLoading}
                                page={this.state.page}
                              />
                            </ListGroup>
                          </CardBody>
                          <CardFooter>
                            <center>
                              <div className="interactions">
                                {this.state.isLoading && <span>Loading...</span>}
                              </div>
                              <div className="interactions">
                                {(this.state.page !== null && !this.state.isLoading) &&
                                  <Button type="button" color="primary" onClick={this.onPaginatedSearch}> Ver Más </Button>
                                }
                              </div>
                            </center>
                          </CardFooter>
                        </Card>
                      </Col>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      )
    }

} export default ObligationsViewer;